import { 
  MapContainer, 
  Marker, 
  Popup, 
  SVGOverlay,
  Tooltip,
  TileLayer, 
  Polyline 
} from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
// import './leaflet07.css'
import './Leaflet.css'
import { iconLocation } from '../MarkerIcon'
import { useEffect, useState } from 'react';
import MarkerPlace from './MarkerPlace';


export default function Leaflet({place, groupPlaces}) {
  const placePosition = [place.location.lat, place.location.lng];
  const [currentLocation, setCurrentLocation] = useState(null);

  console.log("currentLocation", currentLocation ? 1 : 0);
  useEffect(() => {
    navigator.geolocation.getCurrentPosition(function (position) {
      console.log("Latitude is :", position.coords.latitude);
      console.log("Longitude is :", position.coords.longitude);
      setCurrentLocation([
        position.coords.latitude,
        position.coords.longitude,
      ]);
    });
  }, []);

  return (
    <MapContainer center={placePosition} zoom={16}>
      <TileLayer
        attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />
      <MarkerPlace place={place}/>
      {groupPlaces ? groupPlaces.map(item =>
        <>
          <MarkerPlace place={item} isChoosen={place.name===item.name}/>
        </>
      ) : null}

      {currentLocation ? (
          <>
            <Marker position={currentLocation}
              icon={ iconLocation }
            >
            </Marker>
            <Polyline pathOptions={{ color: 'red' }} positions={[placePosition, currentLocation]} />
          </>
        ) : null}
    </MapContainer>
  )
}