import React, { useState, useEffect } from 'react';
import Hero from '../Hero';
import Products from '../Products';
import { productData, productDataTwo } from '../Products/data';
import Feature from '../Feature';
import Footer from '../Footer';
import Tools from '../Tools';
import Map from '../Map';
import { Link, Route, useParams, useRouteMatch } from "react-router-dom";
import VideoSidebar from '../Sidebar/VideoSidebar';
import InfoSidebar from '../Sidebar/InfoSidebar';
import { scrollToBottom, useAutoScrollToBottom } from '../../utils/scroll';
import { atom, useAtom } from 'jotai';


export const placeAtom = atom(null)

function Place() {
  const [isVideoSidebarOpen, setisVideoSidebarOpen] = useState(false);
  const [isInfoSidebarOpen, setIsInfoSidebarOpen] = useState(false);
  const [place, setPlace] = useAtom(placeAtom);
  const [groupPlaces, setGroupPlaces] = useState(null)
  const { placeId } = useParams();

  const toggleVideoSidebar = () => {
    setisVideoSidebarOpen(!isVideoSidebarOpen);
  }
  const toggleInfoSidebar = () => {
    setIsInfoSidebarOpen(!isInfoSidebarOpen);
  }
  useAutoScrollToBottom();

  useEffect(() => {
    let groupPlaceIds = null;
    try {
      setPlace(require(`../../data/places/${placeId}.json`));
      groupPlaceIds = require(`../../data/group-places/dating.json`);
      setGroupPlaces(groupPlaceIds.map(item => require(`../../data/places/${item}.json`)));
    }
    catch {

    }
    
  }, [])
  
  if(place == null) {
    return (<div>Not found</div>)
  }

  return (
    <div>
      <Hero place={place} toggleVideoSidebar={toggleVideoSidebar} isVideoSidebarOpen={isVideoSidebarOpen}/>
      <Map 
        place={place} 
        groupPlaces={groupPlaces}
        toggleVideoSidebar={toggleVideoSidebar}
        toggleInfoSidebar={toggleInfoSidebar}
      />
      {/* <Products heading='Choose your favorite' data={productData} />
      <Feature />
      <Products heading='Sweet Treats for You' data={productDataTwo} />
      <Footer /> */}
      <VideoSidebar isOpen={isVideoSidebarOpen} toggle={toggleVideoSidebar} youtubeEmbedId={place.youtubeEmbedId}/>
      <InfoSidebar isOpen={isInfoSidebarOpen} toggle={toggleInfoSidebar} info={place.info} />
		</div>
  );
}

export default Place;
