import styled from 'styled-components';

export const TiktokImageContainer = styled.div`
  position: relative;
  display: flex;
  margin-left: 5px;
`;

export const TiktokImage = styled.img`
  width: 50px;
  height: 50px;
  opacity: ${({numberCountdown}) => numberCountdown > 0 ? 0.5 : 1};

  &:hover {
    background: #ffc500;
    transition: 0.2s ease-out;
    cursor: pointer;
    color: #000;
  }
`;

export const CountdownNumber = styled.div`
  position: absolute;
  top: 0;
  margin-left: 10px;
  font-size: 44px;
  font-weight: bold;
  color: orange;

  &:hover {
    cursor: pointer;
  }
`;