import React, { useState } from 'react';
import Navbar from '../Navbar';
import Sidebar from '../Sidebar/DefaultSidebar';
import { HeroContainer, HeroContent, HeroItems, HeroH1, HeroP, HeroBtn, TiktokImage, Row } from './HeroElements';
import TiktokImageCountdown from './TiktokImageCountdown/index';
import CopyButton from './CopyButton/index';

const Hero = ({ place, toggleVideoSidebar, isVideoSidebarOpen }) => {
  const [isOpen, setIsOpen] = useState(false);
  
  const toggle = () => {
    setIsOpen(!isOpen);
  };

  const scrollToBottom = () => {
    window.scrollTo({ 
      top: document.documentElement.scrollHeight, 
      behavior: 'smooth'
      /* you can also use 'auto' behaviour 
         in place of 'smooth' */
    }); 
  }

  return (
    <HeroContainer place={place}>
      <Navbar toggle={toggle} />
      <Sidebar isOpen={isOpen} toggle={toggle} />
      
      <HeroContent>
        <HeroItems>
          <HeroH1>{place.name}</HeroH1>
          {/* <HeroP>Ready in 60 seconds</HeroP> */}
          <Row>
            <HeroBtn onClick={scrollToBottom}>Explore</HeroBtn>
            <TiktokImageCountdown onClick={toggleVideoSidebar} isVideoSidebarOpen={isVideoSidebarOpen}/>
          </Row>
          <Row>
            <CopyButton />
          </Row>
        </HeroItems>
      </HeroContent>
      {/* <LandpageTools /> */}
    </HeroContainer>
  );
};

export default Hero;
