import React, { useState } from "react";
import { TiktokImage } from "./styles";

const TiktokButton = ({ place, onClick }) => {
  return (
    <>
      {/* <VideoSidebar isOpen={isVideoSidebarOpen} toggle={toggleVideoSidebar} youtubeEmbedId={place.youtubeEmbedId}/> */}
      <TiktokImage onClick={onClick} src="/assets/tiktok.png" />
    </>
  );
};

export default TiktokButton;
