import React, { useEffect, useState} from 'react';
import {
  SidebarContainer,
  Icon,
  CloseIcon
} from './SidebarElements';
import {useComponentVisible} from '../../../utils/useComponentVisible';
import $ from 'jquery';

const Sidebar = ({ isOpen, toggle, youtubeEmbedId }) => {
  const [videoRef, setVideoRef] = useState(null)
  const { ref } = useComponentVisible(isOpen, () => {
    if(isOpen) {
      toggle();

    }
  });
  
  const source = `https://www.youtube.com/embed/${youtubeEmbedId}?controls=0&enablejsapi=1&loop=1&playlist=${youtubeEmbedId}&showinfo=0`;
  const stopVideo2 = () => {
    var videoURL = videoRef.src;
    videoURL = videoURL.replace("&autoplay=1", "");
    videoRef.src = '';
    videoRef.src = videoURL;
  }
  const pauseVideo = () => {
    videoRef.contentWindow.postMessage('{"event":"command","func":"' + 'pauseVideo' + '","args":""}', '*');
  };
  const playVideo = () => {
    videoRef.contentWindow.postMessage('{"event":"command","func":"' + 'playVideo' + '","args":""}', '*');
  };
  const stopVideo = () => {
    videoRef.contentWindow.postMessage('{"event":"command","func":"' + 'stopVideo' + '","args":""}', '*');
  };

  if(isOpen) {
    playVideo();
  } else {
    try { pauseVideo(); }
    catch(ex) {}
  }

  return (
    <SidebarContainer isOpen={isOpen} onClick={toggle} ref={ref}>
      <div style={{
        height: "75px",
        width: "100%",
        position: "absolute",
        backgroundColor: "#000000"
      }}>
        
      </div>
      <Icon onClick={toggle}>
        <CloseIcon />
      </Icon>
      <iframe width="100%" height="95%" ref={setVideoRef}
        id="youtube"
        src={source}
        title="YouTube video player" 
        frameborder="0" 
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
        allowfullscreen
       
      >
      </iframe>
      <div style={{
        bottom: 0,
        height: "85px",
        width: "100%",
        position: "absolute",
        backgroundColor: "#000000"
      }}>
        
      </div>
    </SidebarContainer>
  );
};

export default Sidebar;
