import React, { useState, useEffect, useRef } from 'react';
import { TiktokImage, CountdownNumber, TiktokImageContainer } from './styles';
import { useScrollActive } from '../../../utils/scroll';

export default function TiktokImageCountdown( { onClick, isVideoSidebarOpen}) {
	const [numberCountdown, setNumberCountdown] = useState(5);
	const [activeTick, setActiveTick] = useState(true);

	const countRef = useRef(numberCountdown);
	countRef.current = numberCountdown;

	const tick = () => {
		setTimeout(() => {
			setNumberCountdown((numberCountdown) => {
				const newNumberCountdown = numberCountdown - 1;
				return newNumberCountdown;
			});
		}, 1000)
	}

	useEffect(() => {
		if(activeTick) {
			if(numberCountdown === 0) {

				onClick();
			} else {
				tick();
			}
		}
		else {
			setNumberCountdown(0);
		}
	
	}, [numberCountdown]);

	useEffect(() => {
		if(isVideoSidebarOpen) {
			setActiveTick(false);
			setNumberCountdown(0);
		}
	}, [isVideoSidebarOpen])

	useScrollActive(() => {
		setActiveTick(false);
	})
	

	return <TiktokImageContainer onClick={onClick}>
		<TiktokImage numberCountdown={numberCountdown} src="/assets/tiktok.png"/>
		{numberCountdown > 0 ? <CountdownNumber>{numberCountdown}</CountdownNumber> : null}
	</TiktokImageContainer>;
}
