import React, { useState, useRef } from 'react';
import { Btn } from './styles';

export default function CopyButton() {
	const [link] = useState(window.location.href);

	const copyToClp = (txt) => {
    var m = document;
    txt = m.createTextNode(txt);
    var w = window;
    var b = m.body;
    b.appendChild(txt);
    if (b.createTextRange) {
        var d = b.createTextRange();
        d.moveToElementText(txt);
        d.select();
        m.execCommand('copy');
    } 
    else {
        var d = m.createRange();
        var g = w.getSelection;
        d.selectNodeContents(txt);
        g().removeAllRanges();
        g().addRange(d);
        m.execCommand('copy');
        g().removeAllRanges();
    }
    txt.remove();
	}

	return <>
		<Btn onClick={() => copyToClp(link)}>Copy link</Btn>
		{/* <input type="text" value={link} id="copyInput" ref={setInputRef}></input> */}
	</>;
}
