import React from 'react'
import { Marker, Tooltip } from 'react-leaflet';
import { iconLocation } from '../MarkerIcon';
import { atom, useAtom } from 'jotai';
import { placeAtom } from '../../Place/index';

export default function MarkerPlace({place, isChoosen}) {
	const [currentPlace, setCurrentPlace] = useAtom(placeAtom);
	const placePosition = [place.location.lat, place.location.lng];

	return (
		<Marker position={placePosition}
			icon={ iconLocation }
			style={{
				zIndex: isChoosen ? 999 : 1
			}}
			eventHandlers={{
				click: () => {
					console.log('marker clicked')
					setCurrentPlace(place)
				},
			}}
		>
			{/* <Popup>
				A pretty CSS3 popup. <br /> Easily customizable.
			</Popup> */}
			<Tooltip 
				permanent 
				direction="bottom" 
				offset={[0, 20]} 	
				style={{
					zIndex: isChoosen ? 999 : 1
				}}
			>
				<div>{place.name}</div>
				{isChoosen ? (
					<>
						<div>{place.address.street}</div>
						<div>{place.address.other}</div>
					</>
				) : null}
				
			</Tooltip>
		</Marker>
	)
}
