import React from 'react';
import { CenterBottom, CenterBottom2 } from './ToolElements';
import ScrollButtonUp from './ScrollButtonUp';
import TiktokButton from './TiktokButton';
import InfoButton from './InfoButton';

export default function Tools({toggleVideoSidebar, toggleInfoSidebar, place}) {
	return (
		<>
			
			<CenterBottom2>
				<ScrollButtonUp />
				<TiktokButton onClick={toggleVideoSidebar} />
				<InfoButton onClick={toggleInfoSidebar} info={place.info} />
			</CenterBottom2>

			{/* <CenterBottom>
				<ScrollButton />
			</CenterBottom> */}
		</>
		
	);
}
