import GoogleMap from "./GoogleMap";
import Tools from '../Tools';
import Leaflet from './Leaflet/Leaflet';

function Map({place, toggleVideoSidebar, toggleInfoSidebar, groupPlaces}) {
  const key = "AIzaSyCD32FcA23Lw1ulQJGA5ROxyP2xcYKQZuQ";

  return (
    <div style={{
      position: "relative",
      height: `100vh`,
    }}>
      {/* <GoogleMap
        place={place}
        googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${key}&callback=initMap`}
        loadingElement={<div style={{ height: `100%` }} />}
        containerElement={
          <div
            style={{
              height: `100vh`,
              margin: `auto`,
              border: "2px solid black",
            }}
          />
        }
        mapElement={<div style={{ height: `100%` }} />}
      /> */}
      <Leaflet 
        place={place}
        groupPlaces={groupPlaces}
      />
      <Tools 
        toggleVideoSidebar={toggleVideoSidebar} 
        toggleInfoSidebar={toggleInfoSidebar} 
        place={place}  
      />
    </div>
  );
}

export default Map;
