import React, { useEffect, useState } from "react";
import {
  withGoogleMap,
  withScriptjs,
  GoogleMap,
  Marker,
  Polyline
} from "react-google-maps";
import InfoBox from "react-google-maps/lib/components/addons/InfoBox";

const options = { closeBoxURL: "", enableEventPropagation: true };
const optionsPolyline = {
  strokeColor: "red",
  strokeOpacity: 0.8,
  strokeWeight: 3,
  fillColor: "#085daa",
  fillOpacity: 0.35,
  clickable: false,
  draggable: false,
  editable: false,
  visible: true,
  radius: 30000,
  zIndex: 1,
};

const Map = ({ place }) => {
  const [currentLocation, setCurrentLocation] = useState(null);

  console.log("currentLocation", currentLocation ? 1 : 0);
  useEffect(() => {
    navigator.geolocation.getCurrentPosition(function (position) {
      console.log("Latitude is :", position.coords.latitude);
      console.log("Longitude is :", position.coords.longitude);
      setCurrentLocation({
        lat: position.coords.latitude,
        lng: position.coords.longitude,
      });
    });
  }, []);

  return (
    <div>
      <GoogleMap
        options={{gestureHandling: 'greedy'}}
        defaultZoom={17}
        defaultCenter={{ lat: place.location.lat, lng: place.location.lng }}
      >
        <Marker
          icon={{
            url: "https://insulationpads.co.uk/wp-content/uploads/2017/10/Home.png",
            scaledSize: new window.google.maps.Size(40, 40),
          }}
          position={{ lat: place.location.lat, lng: place.location.lng }}
        >
          <InfoBox options={options}>
            <>
              <div
                style={{
                  backgroundColor: "#000000",
                  color: "white",
                  borderRadius: "1em",
                  padding: "0.25em",
                  left: -30,
                }}
              >
                <div>{place.address.street}</div>
                <div>{place.address.other}</div>
              </div>
            </>
          </InfoBox>
        </Marker>
        {currentLocation ? (
          <>
            <Marker
              icon={{
                url: "/assets/anonymous.png",
                scaledSize: new window.google.maps.Size(40, 40),
                shape:{coords:[17,17,18],type:'circle'}
              }}
              style={{
                borderRadius: "4em"
              }}
              position={{ lat: currentLocation.lat, lng: currentLocation.lng }}
            ></Marker>
            <Polyline path={[place.location, currentLocation]} options={optionsPolyline} />
          </>
        ) : null}
      </GoogleMap>
    </div>
  );
};

export default withScriptjs(withGoogleMap(Map));
