import React, { useEffect, useState} from 'react';
import { SidebarContainer, Icon, CloseIcon, IconImage, Row, InfoImage } from './SidebarElements';
import {useComponentVisible} from '../../../utils/useComponentVisible';
import $ from 'jquery';

const Sidebar = ({ isOpen, toggle, info }) => {
  const { ref, isComponentVisible } = useComponentVisible(isOpen, () => {
    if(isOpen) {
      toggle();
    }
  });
  

  return (
    <SidebarContainer isOpen={isOpen} onClick={toggle} ref={ref}>
      <div style={{
        height: "75px",
        width: "100%",
        backgroundColor: "#f0f2f5"
      }}>
        
      </div>
      <Icon onClick={toggle}>
        <CloseIcon />
      </Icon>
      <div style={{
        height: "485px",
        width: "100%",
        display: "flex",
        alignItems: "center"
      }}>
        <div style={{
          height: "100%",
          width: "100%",
          backgroundColor: "#ffffff",
          borderRadius: "9px",
          margin: "15px",
          paddingTop: "15px",
          paddingLeft: "10px"
        }}>
          <Row>
            <IconImage style={{ backgroundPosition: "0px -228px" }} />
            <div>Price range: {info.price}</div>
          </Row>
          <Row>
            <IconImage style={{ backgroundPosition: "-21px -312px" }} />
            <div>{info.phone}</div>
          </Row>
          <Row>
            <IconImage style={{ backgroundPosition: "0px -186px" }} />
            <div>Open time: {info.open}</div>
          </Row>
          {info.recommendedMenu == null ? null : (
            <div >
              <Row>
                <InfoImage style={{ backgroundPosition: "0px -302px" }} />
                <div>Recommended:</div>
              </Row>
              <div style={{ paddingLeft: "15px" }}>
                {info.recommendedMenu.map(item => (
                  <Row>
                    - {item}
                  </Row>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>
      <div style={{
        bottom: 0,
        height: "85px",
        width: "100%",
        position: "absolute",
        backgroundColor: "#f0f2f5"
      }}>
        
      </div>
    </SidebarContainer>
  );
};

export default Sidebar;
