import styled from 'styled-components';
export const Btn = styled.button`
  font-size: 1.4rem;
  padding: 0.6rem 3.45rem;
  border: none;
  background: #e31837;
  color: #fff;
  transition: 0.2s ease-out;

  &:hover {
    background: #ffc500;
    transition: 0.2s ease-out;
    cursor: pointer;
    color: #000;
  }
`;